import React, {useState, useEffect} from "react";
import "./style.css";

function UlList({id, items, ulClassName}) {
    return (
        <div id={id} className={ulClassName}>
            {
                items.map(item =>{
                    if(item.children) {
                        return (
                            <React.Fragment>
                            <div className="list-group-item">
                                <a href={"#"+item.idElement} data-toggle="collapse" data-parent="#sidebar">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" style={{marginRight: 10}}><path d="M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z"/></svg>
                                    {item.nombre}
                                </a>
                            </div>
                            <div className="collapse" id={item.idElement}>
                                <UlList items={item.children} ulClassName="level" />
                            </div>
                            </React.Fragment>
                        )
                    } else {
                        return (
                            <div className="list-group-item">
                                <a href={"#"+item.idElement}>
                                    {item.nombre}
                                </a>
                            </div>
                        )
                    }
                })
            }
        </div>
    )
}

function TablaContenido({contenido}) {
    return (
        <UlList id="sidebar" items={contenido} ulClassName="list-group sidebar-content"/>
    )
}

export default TablaContenido;