import React, { useEffect, useState } from 'react';
import 'jquery/src/jquery';
// import Modernizr from 'modernizr';
import './style.css';
var $ = require('jquery');

function escRegex(regex) {
	return regex.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}

function Libro(props) {
	// const [ filtro, setFiltro ] = useState('');
	const [ bookIsRendered, setBookIsRendered ] = useState(false);
	const [ divsFiltro, setDivsFiltro ] = useState([]);

	var contenido = props.contenido.map((term, index) => {
		term.pagina = index + 2;
		return term;
	});

	function cambiarPagina(numPagina) {
		if (numPagina % 2 == 0) {
			$('#' + props.name).turn('page', numPagina);
		} else {
			// Si el número es impar sumele uno para que abra bien la página
			$('#' + props.name).turn('page', numPagina + 1);
		}
	}

	function calcularResultados(filtro) {
		if (filtro.trim() == '') {
			setDivsFiltro([]);
		} else {
			console.log(filtro);
			let filtro2 = escRegex(filtro).replace(/[aeiouáéíóú]/gi, function(vowel) {
				switch (vowel) {
					case 'a':
					case 'A':
					case 'Á':
					case 'á':
						return '[aá]';
					case 'e':
					case 'é':
					case 'E':
					case 'É':
						return '[eé]';
					case 'i':
					case 'í':
					case 'I':
					case 'Í':
						return '[ií]';
					case 'o':
					case 'ó':
					case 'O':
					case 'Ó':
						return '[oó]';
					case 'u':
					case 'ú':
					case 'U':
					case 'Ú':
						return '[uú]';
					default:
						return vowel;
				}
			});
			console.log('filtro2', filtro2);
			let regex = new RegExp(filtro2, 'i');
			console.log('regex', regex);
			var resultadosFiltrados = contenido.filter((term) => regex.test(term.title)
			).slice(0, 10);

			let resultadosBusqueda = [];
			for (let i = 0; i < resultadosFiltrados.length; i++) {
				let resultados = resultadosFiltrados[i];
				let [prefix] = resultados.title.split(regex, 1);
				let tail = resultados.title.replace(new RegExp(`^${escRegex(prefix)}`, 'i'), '');
				let suffix = tail.replace(new RegExp(`^${filtro2}`, 'i'), '');
				let middle = tail.replace(new RegExp(`${escRegex(suffix)}$`, 'i'), '');

				resultadosBusqueda.push(
					<div>
						<a
							style={{ textDecoration: 'underline', cursor: 'pointer' }}
							onClick={() => {
								cambiarPagina(resultados.pagina);
							}}
						>
							{prefix}<span className="highlighted">{middle}</span>{suffix} - Pag: {resultados.pagina}
						</a>
					</div>
				);
			}

			setDivsFiltro(resultadosBusqueda);
		}
	}

	useEffect(() => {
		if (!bookIsRendered) {
			require('../../assets/js/turn.js/turn');
			$('#' + props.name).turn({
				width: 700,
				height: 450,
				autoCenter: true
			});
			setBookIsRendered(true);
		}
	});

	let divsLibro = [];
	for (let i = 0; i < contenido.length; i++) {
		let term = contenido[i];
		let clases = 'page';

		// comprobar si la pagina es par o impar
		if ((i + 1) % 2 == 0) {
			clases += ' even';
		} else {
			clases += ' odd';
		}

		divsLibro.push(
			<div className={clases}>
				<div className="significado">
					<div className="content">
						<h3>{term.title}</h3>
						{term.description}
					</div>
					<div className="numero-pagina">{term.pagina}</div>
				</div>
			</div>
		);
	}

	return (
		<section className="row mb-5">
			<div className="col-8">
				<div id={props.name} className="flipbook sample-flipbook">
					<div class="hard portada">
						<h1 className="titulo">{props.titulo}</h1>
					</div>
					<div class="hard contraportada" />

					{divsLibro}
					<div class="hard" />
					<div class="hard" />
				</div>
			</div>
			<div className="col-4">
				<div >
					<div>
					<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M23.111 20.058l-4.977-4.977c.965-1.52 1.523-3.322 1.523-5.251 0-5.42-4.409-9.83-9.829-9.83-5.42 0-9.828 4.41-9.828 9.83s4.408 9.83 9.829 9.83c1.834 0 3.552-.505 5.022-1.383l5.021 5.021c2.144 2.141 5.384-1.096 3.239-3.24zm-20.064-10.228c0-3.739 3.043-6.782 6.782-6.782s6.782 3.042 6.782 6.782-3.043 6.782-6.782 6.782-6.782-3.043-6.782-6.782zm2.01-1.764c1.984-4.599 8.664-4.066 9.922.749-2.534-2.974-6.993-3.294-9.922-.749z"/></svg>
						<label className="label-buscador" for="buscador">Búsqueda</label></div>
					<input
						id="buscador"
						placeholder="Ingrese la palabra"
						// value={filtro}
						onChange={(e) => {
							// setFiltro(e.target.value);
							calcularResultados(e.target.value);
						}}
					/>
				</div>
				<div> {divsFiltro}</div>
			</div>
		</section>
	);
}

export default Libro;
