import React from 'react';
// import './App.css';
import Header from './layout/header';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Footer from './layout/footer';
import UnidadesTematicas from './pages/UnidadesTematicas';
import Portada from './pages/Portada';
import Glosario from './pages/Glosario';
import Contacto from './pages/Contacto';
import Bibliografia from './pages/Bibliografia';
import Ejercicio from './pages/Ejercicio';
import Foro from './pages/Foro';
import UnidadUno from './pages/UnidadUno';
import UnidadDos from './pages/UnidadDos';
import UnidadTres from './pages/UnidadTres';
import UnidadCuatro from './pages/UnidadCuatro';
import UnidadCinco from './pages/UnidadCinco';
function App() {
	return (
		<Router>
			<Header />

			<div>
				{/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
				<Switch>
				<Route path="/foro.html">
						<Foro/>
					</Route>
					<Route path="/bibliografia.html">
						<Bibliografia/>
					</Route>
					<Route path="/glosario.html">
						<Glosario/>
					</Route>
					<Route path="/unidades-tematicas.html">
						<UnidadesTematicas />
					</Route>
					<Route path="/unidad-uno.html">
						<UnidadUno/>
					</Route>
					<Route path="/unidad-dos.html">
						<UnidadDos/>
					</Route>
					<Route path="/unidad-tres.html">
						<UnidadTres />
					</Route>
					<Route path="/unidad-cuatro.html">
						<UnidadCuatro/>
					</Route>
					<Route path="/unidad-cinco.html">
						<UnidadCinco/>
					</Route>
					<Route path="/ejercicios.html">
						<Ejercicio/>
					</Route>
					<Route path="/contacto.html">
						<Contacto/>
					</Route>
					<Route path="/">
						<Portada />
					</Route>
				</Switch>
			</div>
			<Footer />
		</Router>
	);
}

export default App;
